.homeSection{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.homeSection> div{
  width: 22%;
  height: 80vh;
  border-radius: 2em;
  box-shadow:
    10px 30px 24px 6px rgba(50, 50, 93, 0.25),
    -10px 18px 36px 6px rgba(0, 0, 0, 0.3),
    20px 25px 60px 6px rgba(0, 0, 0, 0.025);
  overflow: hidden;
}
.homeSection> div> a{
  border-radius: 25px;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  transition: 0.2s all;
}
.homeSection> div> a:hover{
  transform: scale(1.3);
}
.reactBox{
  background: url(../images/reactHook.png) no-repeat;
}
.javaScriptBox{
  background: url(../images/javaScript.png) no-repeat;
}
.noteBox{
  background: url(../images/localStorage.png) no-repeat;
}
.cssBox{
  background: url(../images/css.png) no-repeat;
}

@media screen and (max-width: 799px) {
  main{
    height: 80vh;
  }
  .homeSection{
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
  }
  .homeSection> div{
    width: 100%;
    height: 18vh;
  }
}




