.detail {
  position:fixed;
  z-index: 99;
  width: 100%;
  height:100vh;
  right:0; 
  top: 0;
  color: rgb(222, 222, 222);
  font-weight: 500;
  transition: 0.3s all;
  background-color: rgba(60, 60, 60, 0.5);
  transform: scale(0);
}
.detail>div{
  position: relative;
  width:100%;
  height:100%;
}
.detail>div>article{
  right:50px;
  position:absolute;
  width:35%;
}
.detail>div>a{
  position:absolute;
  top:20%;
  right:10px;
  z-index: 2;
  font-size:2.5rem;
  box-shadow: 1px 1px 3px rgb(0, 0, 0);
  border-radius: 10px;
}
.detailBox{
  position: absolute;
  height:80%;
  bottom:1rem;
  background-size: cover;
  background-position: cover;
  z-index:1;
  box-shadow: 
    12px 12px 30px rgba(50, 50, 93, 0.25),
    18px 12px 60px rgba(0, 0, 0, 0.3),
    12px 12px 30px rgba(0, 0, 0, 0.025);
}
.detail .titleBox{
  display: flex;
  flex-direction:column;
  width:55%;
  height:60%;
  right:40%;
  bottom:7rem;
  grid-gap: 1.5rem;
  color:beige;
  font-weight: 600;
  padding:1rem 1rem;
  box-shadow: 
    0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3),
    0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  text-shadow: 1px 1px 1px rgb(0, 0, 0);
  overflow-y:auto;
  border-radius: 20px;
}
.titleBox h1{
  font-size:2rem;
}
.titleBox a:hover{
  color:beige;
}
.titleBox>div>div, .titleBox ul{
  display:flex;
  flex-direction: row;
  grid-gap: 1rem;
}

@media screen and (max-width:1023px) {
  .detail .detailBox{
    width:90%;
    height:90%;
    top:5%;
    left:5%;
  }
  .detail>div>a{
    top:10%;
    right:10%;
  }
  .detail .titleBox{
    width:80%;
    z-index:11;
    top:25%;
    left:8%;
    text-shadow: 1px 1px 2px rgb(0, 0, 0);
    background-color:rgba(0,0,0,0.5);
  }
}




.loader{
  width:100%;
  height:100%;
  overflow:hidden;
}
  
.loaderBox{
  position:relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width:100%;
  height:100%;
}
.Phrase{
  color:#fff;
  font-size:7rem;
  font-weight: 600;
  position:absolute;
  padding-left:8vw;
}
.loaderBox div{
  margin-top:10vw;
  position:relative;
  width:20vw;
  height:20vw;
  border-radius: 50%;
  border:8px solid transparent;
}
.loaderBox div:before{
  position:absolute;
  content:'';
  width:10%;
  height:10%;
  border-radius: 50%;
}
.loaderBox .rotation1{
  border-bottom: 8px solid rgb(56, 172, 66);
  animation: rotate1 4s linear infinite;
}
@keyframes rotate1{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
.rotation1:before{
  background-color:rgb(56, 172, 66);
  box-shadow: 
    0px 0px 0px 10px rgba(56, 172, 66, 0.6),
    0px 0px 0px 20px rgba(56, 172, 66,0.4),
    0px 0px 0px 30px rgba(56, 172, 66, 0.2),
    0px 0px 40px rgba(56, 172, 66),
    0px 0px 60px rgba(56, 172, 66);
    top:82%;
    left:8%;
}

.loaderBox .rotation2{
  margin-left:-8vw;
  border-right:8px solid rgba(175, 78, 78, 1);
  animation: rotate2 3.6s linear infinite;
}
@keyframes rotate2{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
.rotation2::before{
  background-color:rgba(175, 78, 78, 1);
  box-shadow: 
  0px 0px 0px 11px rgba(175, 78, 78,0.6),
  0px 0px 0px 20px rgba(175, 78, 78,0.4),
  0px 0px 0px 30px rgba(175, 78, 78,0.2),
  0px 0px 40px rgba(175, 78, 78),
  0px 0px 60px rgba(175, 78, 78);
  top:82%;
  left:82%;
}

.loaderBox .rotation3{
  margin-top:-14vw;
  margin-left:-28vw;
  border-left:8px solid rgba(102, 132, 214, 1);
  animation: rotate3 3.8s linear infinite;
}
@keyframes rotate3{
  0%{
    transform: rotate(360deg);
  }
  100%{
    transform: rotate(0deg);
  }
}
.rotation3::before{
  background-color: rgba(102, 132, 214, 1);
  box-shadow: 
  0px 0px 0px 10px rgba(102, 132, 214,0.6),
  0px 0px 0px 20px rgba(102, 132, 214,0.4),
  0px 0px 0px 30px rgba(102, 132, 214,0.2),
  0px 0px 40px rgba(102, 132, 214),
  0px 0px 60px rgba(102, 132, 214);
  top:82%;
  left:8%;
}

@media screen and (max-width:1023px) {
  .loaderBox{
    width:70vw;
    height:70vw;
    margin:0 auto;
  }
}
@media screen and (max-width:530px) {
  
  .Phrase{
    font-size: 4rem;
    font-weight: 600;
    padding-left: 0;
  }
  .loaderBox .rotation1{
    border-right: 8px solid rgb(56, 172, 66);
  }
  .loaderBox .rotation2{
    border-top:8px solid rgb(175, 78, 78);
  }
  .loaderBox .rotation3{
    border-top:8px solid rgb(102, 132, 214);
  }
}