@font-face {
  font-family: 'SF_IceMango';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_IceMango.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}  
  
  /*reset*/
  *{
    margin:0;
    padding:0;
  }
  html{
    width:100%;
    height:100%;
    font-size:62.5%;
    color:#262626;
    font-size:1rem;
  }
  ul, dl{
    list-style:none;
  }
  fieldset{
    border:0 none;
  }
  legend{
    font-size:0;
  }
  table{
    border-collapse:collapse;
    border-spacing:0;
  }
  a:visited, a:hover, a:active, a:link{
    text-decoration:none;
    color:#262626;
  }
  a,img{
    display:block;
  }
  address,em{
    font-style:normal;
  }
  caption,legend{
    width:0; 
    height:0; 
    font-size:0; 
    line-height:0;
  }
  em{
    cursor:pointer; 
    font-style:normal;
  }
  img,input,textarea,select,button{
    border:0 none; 
    vertical-align:middle;
  }
  button{
    cursor:pointer;
  }
  button:visited,button:active{
    border:0 none;
  }
  button:focus,input:focus,select:focus {
    outline:none;
  }
/*reset*/
body{
  width:100%;
  height:100%;
}
#root{
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
  display:flex;
  flex-direction: column;
  align-items: center;
}
.hidden {
  display:none; 
  overflow:hidden;
}
header{
  position:relative;
  width:90vw;
  height:10vh;
  min-height:10vh;
  display:flex;
  flex-direction: row;
  justify-content:space-between;
  align-items: center;
}
main{
  width:90vw;
  height:80vh;
  position:relative;
}
footer{
  width:90vw;
  height:10vh;
  min-height:10vh;
  position:relative;
}