
header >section{
  display: flex;
  flex-direction: row;
}
.titleBox h1{
  font-size:2em;
  letter-spacing: 10px;
  text-shadow: 
    4px 4px 4px rgba(112, 112, 112,0.6),
    4px 4px 6px rgba(112, 112, 112,0.4),
    4px 4px 12px rgba(112, 112, 112,0.4);
}
.listBox{
  overflow:hidden;
}
.listBox ul{
  position:relative;
  display: flex;
  flex-direction: row;
  grid-gap: 2rem;
  margin-right:2rem;
}
.listBox li{
  overflow:hidden;
}

.listBox li a{
  font-weight: 600;
  letter-spacing: 2px;
  opacity:0;
  transform: translateX(100%);
  transition: 0.5s all;
  transition-delay: calc(var(--i) * 0.1s);
}
.listBox .onNavTag{
  opacity:1;
  transform: translateX(0%);
}
.onNavTag span{
  position:absolute;
  width:0;
  height:100%;
  left:0px;
  border-bottom: 2px solid #262626;
  transition: 0.3s all;
}
.onNavTag:hover span{
  width:120%;
}

.listBox li:nth-child(2) a{
  transition-delay: 0.1s;
}
.listBox li:nth-child(3) a{
  transition-delay: 0.2s;
}
.listBox li:nth-child(4) a{
  transition-delay: 0.3s;
}

.listBox div{
  position: relative;
  cursor: pointer;
  width:32px;
  height:32px;
  border-top:2px solid rgb(0, 0, 0);
  background:none;
  overflow:hidden;
  transition:1s all;
}
.listBox div span{
  position:absolute;
  width:32px;
  left:0;
  transition:1s all;
}
.listBox div span:first-child{
  height:15px;
  top:13px;
  border-top:2px solid #000;
}
.listBox div span:last-child{
  height:28px;
  top:0;
  border-bottom:2px solid #000;
}
.listBox .onBtn{
  border-top-color:rgba(0,0,0,0);
}
.listBox .onBtn span:first-child{
  width:40px;
  top:13px;
  left:1px;
  border-top:2px solid #000;
  transform: rotate(-45deg);
}
.listBox .onBtn span:last-child{
  width:40px;
  top:-9px;
  left:6px;
  border-bottom:2px solid #000;
  transform: rotate(45deg);
}
@media screen and (max-width:767px) {
  .listBox ul{
    display:none;
  }
  .onNav{
    position:absolute;
    background-color: rgb(255,255,255);
    z-index: 99;
    top:0;
    left:-10%;
    width:116%;
    height:100vh;
  }
  .onNav ul{
    display:flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 2rem;
    width:100%;
    margin-top:5rem;
    font-size:3rem;
  }
  .onNav div{
    position:absolute;
    top:3rem;
    right:2rem;
    transition: 0s all;
  }
  .listBox div{
    transition: 0s all;
  }
  .listBox div span{
    transition: 0s all;
  }
  .onNav .onBtn span:first-child{
    top:13px;
  }
}