.movie {
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #2c2c2c;
  display: grid;
  grid-template-columns: minmax(20%, 1fr) 2fr;
  grid-gap: 5%;
  box-shadow: 
    0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),
    0 -6px 16px -6px rgba(0, 0, 0, 0.025)
  ;
}

.movie_img {
  position: relative;
  top: -5rem;
  width: 100%;
  box-shadow: 
    0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3),
    0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie_title,
.movie_year {
  margin: 0;
  font-weight: 700;
  text-decoration: none;
}

.movie_title a {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
  text-decoration: none;
}

.movie_genres {
  list-style: none;
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
}

.movie_genres li{
  margin-right: 10px;
  font-size: 14px;
  font-weight: 700;
}

@media screen and (max-width:799px) {
  .movie h2 a{
    font-size:3rem;
  }
  .movie h3{
    font-size:2rem;
  }
  .movie P{
    font-size:1.8rem;
  }
  .movie li{
    font-size:2rem;
  }
}