
.animation_rotate_20s{
  animation: rotate 20s linear infinite;
}
@keyframes rotate{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

.animation_mouseIconDown{
  display:flex;
  flex-direction: column;
  align-items: center;
  position:absolute;
  top:18rem;
  right:8rem;
  width:4rem;
  height:6rem;
  background-color:rgb(250, 250, 250);
  border-radius: 15px;
  box-shadow: 2px 2px 4px rgba(10, 10, 10, 0.8),
  2px 4px 6px rgba(3, 3, 3, 0.6),
  4px 4px 8px rgba(10, 10, 10, 0.4);
  overflow:hidden;
}
.animation_mouseIconDown::before{
  content: '';
  display: block;
  width:0.8rem;
  height:1.3rem;
  border:1px solid rgba(120,120,120,0.6);
  border-radius: 15px;
  margin-top:1rem;
  background-color:rgb(190,190,190,0.8);
}
.animation_mouseIconDown::after{
  content: '';
  display: block;
  position: absolute;
  width:1rem;
  height:1rem;
  border:4px double #000;
  top:3.6rem;
  transform: rotate(45deg);
  border-top:transparent;
  border-left:transparent;
  animation: mouseDown 1.2s linear infinite;
}
@keyframes mouseDown {
  0%{
    top:2.5rem;
  }
  100%{
    top:3.6rem;
  }
}

.animation_mouseIconUp{
  display:flex;
  flex-direction: column;
  align-items: center;
  position:absolute;
  top:18rem;
  right:8rem;
  width:4rem;
  height:6rem;
  background-color:rgb(250, 250, 250);
  border-radius: 15px;
  box-shadow: 2px 2px 4px rgba(10, 10, 10, 0.8),
  2px 4px 6px rgba(3, 3, 3, 0.6),
  4px 4px 8px rgba(10, 10, 10, 0.4);
  overflow:hidden;
}
.animation_mouseIconUp::before{
  content: '';
  display: block;
  width:0.8rem;
  height:1.3rem;
  border:1px solid #000;
  border-radius: 15px;
  margin-top:1rem;
}
.animation_mouseIconUp::after{
  content: '';
  display: block;
  position: absolute;
  width:1rem;
  height:1rem;
  border:4px double #000;
  top:3.6rem;
  transform: rotate(225deg);
  border-top:transparent;
  border-left:transparent;
  animation: mouseUp 1.2s linear infinite;
}
@keyframes mouseUp {
  0%{
    top:4.5rem;
  }
  100%{
    top:3.5rem;
  }
}

.animation_rightMoving{
  animation: rightMoving 1s linear;
  position:relative;
}
@keyframes rightMoving {
  0%{
    left:-100%;
  }
  10%{
    left:-40%;
  }
  100%{
    left:0;
  }
}
.animation_leftMoving{
  animation: leftMoving 1s linear;
  position:relative;
}
@keyframes leftMoving {
  0%{
    right:-100%;
    opacity:0
  }
  100%{
    right:0;
    opacity:1;
  }
}

.animation_rotateX{
  animation: rotateX 1s linear;
  position: relative;
  transform:rotateX(0deg);
}
@keyframes rotateX {
  0%{
    transform:rotateX(90deg);
    opacity:0;
  }
  
  100%{
    opacity:1;
    transform:rotateX(0deg);
  }
}
.animation_scaleX{
  animation: scaleX 1s linear;
  position: relative;
  transform:scaleX(1);
}
@keyframes scaleX {
  0%{
    transform:scaleX(0);
    opacity:0;
  }
  
  100%{
    opacity:1;
    transform:scaleX(1);
  }
}

.animation_lightEffect{
  position:absolute;
  width:100%;
  height:5px;
  left:0px;
  background-color:rgba(255, 255, 255, 1);
  box-shadow: 
    1px 2px 2px rgba(255,255,255,0.8),
    1px -2px 2px rgba(255,255,255,0.8),
    2px 6px 6px rgba(255,255,255,0.4),
    2px -6px 6px rgba(255,255,255,0.4);
  opacity:0;
  animation: lightEffect 6s linear infinite;
}
@keyframes lightEffect {
  0%{
    transform: translate(0px, 30px) rotate(-10deg);
  }
  9%{
    transform: translate(0px, 130px) rotate(-10deg);
    width:120%;
    opacity:1;
  }
  10%{
    transform: translate(0px, 130px) rotate(-10deg);
    width:100%;
    opacity:0;
  }
}

.animation_shadowEffect{
  animation: shadowEffect 6s linear infinite;
}
@keyframes shadowEffect {
  0%{
    text-shadow: 
    1px 1px 6px rgba(0, 0, 0, 0.4);
  }
  5%{
    text-shadow: 
    -8px -8px 6px rgba(0, 0, 0, 0.4);
  }
  15%{
    text-shadow: 
    1px 1px 6px rgba(0, 0, 0, 0.4);
  }
}