.reactMain{
  height:initial;
}
.container {
  display: flex;
  flex-direction: row;
  flex-wrap:nowrap;
}
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-weight: 600;
  font-size:10rem;
  color:#2c2c2c;
}
.movies {
  display: grid;
  width: 80%;
  grid-template-columns: repeat(2, minmax(40%, 1fr));
  grid-gap: 2rem;
  padding-top: 70px;
}
.movies li{
  display:inline;
  margin-right:10px;
}
.movies>div>div {
  overflow: hidden;
}
.navBar {
  position:relative;
  margin-top:50px;
  padding:10px;
  width:20%;
  max-width:15rem;
}
.navBar>div {
  position:sticky;
  top:10rem;
  min-width:135px;
  max-width:160px;
  color:#2c2c2c;
}
.navBar h2{
  margin-bottom:20px;
  text-align:center;
}
.navBar span {
  margin-right:0px;
}
.navBar select {
  width:40%;
  text-align: center;
}

@media screen and (max-width: 1023px) {
  .container{
    flex-direction: column;
  }
  .movies {
    grid-template-columns: 1fr; 
    width: 100%;

  }
  .navBar{
    width:100%; 
    min-width:100%; 
    padding:0; 
    display:flex; 
    justify-content: center
  }
  .navBar>div{
    position:static;
    width:80%; 
    min-width:80%; 
    display:flex; 
    justify-content: flex-end
  }
  .navBar>div>select, .navBar>div>h2, .navBar>div>span{
    margin:0; padding:0; font-size:2rem;
  }
  .navBar>div>h2{
    margin-right:2rem;
  }
  .navBar>div>select{
    width:25%;
  }
}


.loader{
  width:80%;
  overflow:hidden;
}
  
.loaderBox{
  position:relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width:100%;
  height:100%;
}
.Phrase{
  color:#2c2c2c;
  font-size:7rem;
  font-weight: 600;
  position:absolute;
  padding-left:8vw;
}
.loaderBox div{
  margin-top:10vw;
  position:relative;
  width:20vw;
  height:20vw;
  border-radius: 50%;
  border:8px solid transparent;
}
.loaderBox div:before{
  position:absolute;
  content:'';
  width:10%;
  height:10%;
  border-radius: 50%;
}
.loaderBox .rotation1{
  border-bottom: 8px solid rgb(56, 172, 66);
  animation: rotate1 4s linear infinite;
}
@keyframes rotate1{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
.rotation1:before{
  background-color:rgb(56, 172, 66);
  box-shadow: 
    0px 0px 0px 10px rgba(56, 172, 66, 0.6),
    0px 0px 0px 20px rgba(56, 172, 66,0.4),
    0px 0px 0px 30px rgba(56, 172, 66, 0.2),
    0px 0px 40px rgba(56, 172, 66),
    0px 0px 60px rgba(56, 172, 66);
    top:82%;
    left:8%;
}

.loaderBox .rotation2{
  margin-left:-8vw;
  border-right:8px solid rgba(175, 78, 78, 1);
  animation: rotate2 3.6s linear infinite;
}
@keyframes rotate2{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
.rotation2::before{
  background-color:rgba(175, 78, 78, 1);
  box-shadow: 
  0px 0px 0px 11px rgba(175, 78, 78,0.6),
  0px 0px 0px 20px rgba(175, 78, 78,0.4),
  0px 0px 0px 30px rgba(175, 78, 78,0.2),
  0px 0px 40px rgba(175, 78, 78),
  0px 0px 60px rgba(175, 78, 78);
  top:82%;
  left:82%;
}

.loaderBox .rotation3{
  margin-top:-14vw;
  margin-left:-28vw;
  border-left:8px solid rgba(102, 132, 214, 1);
  animation: rotate3 3.8s linear infinite;
}
@keyframes rotate3{
  0%{
    transform: rotate(360deg);
  }
  100%{
    transform: rotate(0deg);
  }
}
.rotation3::before{
  background-color: rgba(102, 132, 214, 1);
  box-shadow: 
  0px 0px 0px 10px rgba(102, 132, 214,0.6),
  0px 0px 0px 20px rgba(102, 132, 214,0.4),
  0px 0px 0px 30px rgba(102, 132, 214,0.2),
  0px 0px 40px rgba(102, 132, 214),
  0px 0px 60px rgba(102, 132, 214);
  top:82%;
  left:8%;
}


@media screen and (max-width:1023px) {
  .loaderBox{
    width:70vw;
    height:70vw;
    margin:0 auto;
  }
}
@media screen and (max-width:530px) {
  
  .Phrase{
    font-size: 4rem;
    font-weight: 600;
    padding-left: 0;
  }
  .loaderBox .rotation1{
    border-right: 8px solid rgb(56, 172, 66);
  }
  .loaderBox .rotation2{
    border-top:8px solid rgb(175, 78, 78);
  }
  .loaderBox .rotation3{
    border-top:8px solid rgb(102, 132, 214);
  }
}