.noteMain{
  height:initial;
}
.noteSection{
  position: relative;
  width:100%;
  min-height:80vh;
  display:flex;
  flex-direction: column;
  align-items: center;
}
.noteTitle{
  position:relative;
  font-size:6rem;
  overflow:hidden;
  letter-spacing: 5px;
}
.formBox{
  display:flex;
  flex-direction: row;
  width:100%;
  height:15vh;
}
.formBox label h1, .formBox label input{
  width:80%;
  cursor: default;
}
.formBox form label input{
  border-bottom:1px solid rgba(110, 110, 110, 0.7);
  height:0px;
  margin-top:-25px;
  padding:0 1rem;
  transition: 0.3s all;
  opacity:0.3;
}
.formBox form label input:focus{
  margin:0;
  height:3rem;
  border:1px solid rgba(110, 110, 110, 0.7);
  font-size:2rem;
  border-radius: 10px;
  opacity:0.7;
}
