.inputBox{
  width:60%;
  flex-grow: 3;
}
.searchBox{
  width:30%;
  flex-grow: 1;
}
.searchBox p{
  padding:1rem;
  font-weight: 600;
  text-shadow: 0px 0px 6px rgba(222, 66, 66, 0.7);
}
.noteSection .listBox{
  margin-top:2rem;
  width:90%;
}
.noteSection .listBox ol{
  list-style: inside inside;
  font-size:1.3rem;
  text-shadow: 0px 0px 6px rgba(110, 110, 110, 0.7);
}
.noteSection .listBox ol li{
  margin:0.5rem 0;
  border-bottom:1px solid rgba(110, 110, 110, 0.7);
}
.noteSection .listBox li button{
  float:right;
}
.noteSection .listBox li:after{
  display:block;
  content:'';
  clear:both;
}
@media screen and (max-width:799px) {
  .formBox{
    flex-direction: column;
  }
  .formBox>article{
    width:100%;
  }
  .searchBox p{
    padding:0;
  }
}